*, *:after, *:before {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding-top: 50px;
	background: $gray;
	font-family: $roboto;
}

ul, ol {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {}

canvas {
  margin: 0 auto;
  max-width: 500px;
  width: 100%;
  max-height: 500px;
  height: 500px;
  box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.75);
}